import React, { Component } from 'react';
import moment from 'moment';
import { requestHeaderDELETEWithAuth, requestHeaderGETWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import Table from 'react-bootstrap/Table';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CTextarea
  } from '@coreui/react';
import CIcon from '@coreui/icons-react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import { brandSet } from '@coreui/icons';
import MaterialReactTable from 'material-react-table';
import Select from 'react-select';
import { yesAndNoList, unitOfMeasurementsList ,paymentInstrumentsList } from 'src/Utils/Enums';
import { BACKEND_PRICE_TRACKER_SERVICE_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';
import store from 'src/store';

//default
const FILE_NAME = "ExpensesView.js";

//default
const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    locationListData: [],
    storeListData: [],
    categoryListData: [],
    productListData: [],
    expensesListData: [],
    filterItems : {
        dateOfPurchaseFilter: null,
        paymentInstrumentFilter: null,
        productFilter: null
    },
    filtersEngaged: false,
    filteredExpensesListData: [],
};


class ExpensesView extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    //default
    componentDidMount() {
        document.title = "Expenses - view";
        this.fetchLocationDataList();
    }

    //default
    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    //default
    reloadData() {
        this.resetStateToDefault();
        this.fetchLocationDataList();
    }

    fetchLocationDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/location/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchLocationDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        locationListData: result.data
                    });
                    this.fetchStoreDataList();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchLocationDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    fetchStoreDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/store/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchStoreDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        storeListData: result.data
                    });
                    this.fetchCategoryDataList();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchStoreDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    fetchCategoryDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/category/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchCategoryDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        categoryListData: result.data
                    });
                    this.fetchProductDataList();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchCategoryDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    fetchProductDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/product/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchProductDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    const productListDataSelectTagSpecific = result.data.map((product) => {
                        return {
                            product,
                            value: product.id,
                            label: product.name,
                            category: this.state.categoryListData.find(category => category.id === product.categoryId)
                        };
                    });
                    this.setState({
                        productListData: result.data,
                        productListDataSelectTagSpecific
                    });
                    this.fetchExpenseDataList();
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchProductDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    fetchExpenseDataList() {
        let API_URL = BACKEND_PRICE_TRACKER_SERVICE_BASE_URL + '/expense/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "fetchExpenseDataList", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        isLoaded: true,
                        expensesListData: result.data,
                        filteredExpensesListData: result.data
                    });
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "fetchExpenseDataList", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    printStoreData(storeId) {
        const store = this.state.storeListData.find(store => store.id === storeId);
        return store.name + ' (🆔 :' + store.id + ') ' + this.printLocationData(store.locationId);
    }

    printLocationData(locationId) {
        const location = locationId === null ? null : this.state.locationListData.find(location => location.id === locationId);
        const locationStr = location === null ? 'N/A' : (location.name + '(' + location.id + ")" + ", " + location.description + ", " + location.city);
        return "📍 LOCATION : " + locationStr;
    }

    toggleFilter() {
        //this function will toggle the filter(s)
        let filteredExpenses = this.state.expensesListData;
        const filterItems = this.state.filterItems;

        filterItems.dateOfPurchaseFilter = document.getElementById('dateOfPurchaseFilter').value;            
        this.setState({
            filterItems
        });

        if((this.state.filterItems.dateOfPurchaseFilter === null || this.state.filterItems.dateOfPurchaseFilter.length <= 0)
            && this.state.filterItems.paymentInstrumentFilter === null 
            && this.state.filterItems.productFilter === null) {
            alert("ℹ️ No filtering-value added.");
            return;
        }

        //engage `dateOfPurchaseFilter`
        if(this.state.filterItems.dateOfPurchaseFilter !== null) {
            filteredExpenses = filteredExpenses.filter(expense => expense.dateAndTimeOfPurchase.includes(this.state.filterItems.dateOfPurchaseFilter));
        }

        //engage `paymentInstrumentFilter`
        if(this.state.filterItems.paymentInstrumentFilter !== null) {
            filteredExpenses = filteredExpenses.filter(expense => expense.payment.paymentInstrument.includes(this.state.filterItems.paymentInstrumentFilter));            
        }

        //engage `productFilter`
        if(this.state.filterItems.productFilter !== null) {
            const filteredProductId = this.state.filterItems.productFilter.product.id;
            filteredExpenses = filteredExpenses.filter(expense => {
                const itemsArray = expense.items;
                return itemsArray.filter(item => item.productId === filteredProductId).length > 0;
            });            
        }

        this.setState({
            filteredExpensesListData: filteredExpenses,
            filtersEngaged : !this.state.filtersEngaged
        });
    }

    //default
    render() {
        //default
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
            this.state.isLoaded ? 
            <div>
                <h1>EXPENSES VIEW &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 
                    <CButton color="success" className="px-4" onClick={() => this.reloadData()}>Reload Data</CButton>
                </h1>
                <div style={{
                        width:'70%',
                        borderStyle:"solid",
                        borderColor:"green",
                        padding: "10px 10px 10px 10px",
                        margin: "0px 0px 10px 0px"
                    }}>
                    <p>Filter applied ? : {this.state.filtersEngaged ? yesAndNoList[0].value : yesAndNoList[1].value } </p>
                    
                    <h4><CIcon size='lg' name="cilLocationPin" />&nbsp;&nbsp; date of purchase : </h4>
                    <CInput
                        placeholder="Date Of Purchase Filter" 
                        autoComplete="on"
                        type="date"
                        step="any"
                        id="dateOfPurchaseFilter"
                        label="Date Of Purchase Filter"
                        required
                    />
                    
                    
                    <h4><CIcon size='lg' name="cilLocationPin" />&nbsp;&nbsp; payment Instrument :</h4>
                    <Select 
                        onChange={(choice) => {
                            const filterItems = this.state.filterItems;
                            filterItems.paymentInstrumentFilter = (choice === null) ? null : choice.value;
                            this.setState({
                                filterItems
                            });
                        }}
                        options={paymentInstrumentsList}
                        isClearable={true}
                    />


                    <h4><CIcon size='lg' name="cilLocationPin" />&nbsp;&nbsp; product :</h4>
                    <Select 
                        onChange={(choice) => {
                            const filterItems = this.state.filterItems;
                            filterItems.productFilter = choice;
                            this.setState({
                                filterItems
                            });
                        }}
                        options={this.state.productListDataSelectTagSpecific}
                        isClearable={true}
                    />
                    <br />
                    <CButton color="warning" className="px-4" onClick={() => this.toggleFilter()}>Enable/apply/engage filters ?</CButton>
                    
                </div>
                <h3>table count : Σ {this.state.filteredExpensesListData.length}</h3>
                <br />
                <div style={{
                    maxHeight: '1000px',
                    overflow: 'auto'
                }}>
                <Table striped bordered hover color='success' style={{
                    textAlign:'left',
                    position: 'relative'
                }}>
                    <thead>
                        <tr style={{
                                position:'sticky',
                                background: 'white',
                                top: 0
                                }}>
                            <th>DB ID#</th>
                            <th>dateAndTimeOfPurchase</th>
                            <th>payment</th>
                            <th>items</th>
                            <th>mode</th>
                            <th>store</th>
                            <th>location</th>
                            <th>extraNotes</th>
                            <th>tags</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.filteredExpensesListData.map(
                                expense => 
                                <tr key={"purchase_id_" + expense.id}>
                                    <td>{expense.id}</td>
                                    <td>{expense.dateAndTimeOfPurchase}</td>

                                    <td>
                                        <tr>
                                            <th>totalAmount</th>
                                            <th>paymentInstrument</th>
                                            <th>bank</th>
                                        </tr>
                                        <tr>
                                            <td>₹{expense.payment.totalAmount}</td>
                                            <td>{expense.payment.paymentInstrument}</td>
                                            <td>{expense.payment.bank}</td>
                                        </tr>
                                    </td>

                                    <td>
                                        <tr>
                                            <th>#</th>
                                            <th>product name</th>
                                            <th>quantity</th>
                                            <th>amount</th>
                                        </tr>

                                        {
                                            expense.items.map(
                                                (item, count) => {
                                                    const product = this.state.productListData.find(product => product.id === item.productId);
                                                    const category = this.state.categoryListData.find(category => category.id === product.categoryId);
                                                    return <tr key={"expense_item_id_" + item.id}>
                                                                <td>{count + 1}</td>
                                                                <td>{product.name} ({product.id})</td>
                                                                <td>{item.quantity} {unitOfMeasurementsList.find(unitOfMeasurement => unitOfMeasurement.value === category.unitOfMeasurement).label}</td>
                                                                <td>₹ {item.amount}</td>
                                                            </tr>
                                            })
                                        }
                                    </td>

                                    <td>{expense.mode}</td>
                                    <td>{this.printStoreData(expense.storeId)}</td>
                                    <td>{this.printLocationData(expense.locationId)}</td>
                                    <td>{expense.extraNotes}</td>
                                    <td>{expense.tags}</td>
                                </tr>
                            )
                        }
                    </tbody>
                </Table>
                </div>

                {/* //default */}
                <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                <FailureModal visible={this.state.failureModalVisible} />
            </div>
            : <Loading />
        );
    }
}

export default ExpensesView;
